import React, { Component } from 'react';

export default class Footer extends Component {
    constructor(props) {
        super(props);
        this.state = { tinder_buttons: true };
    }

    renderTinderButtons = () => {
        return (
            <footer className="footer-container">
                <img
                    onClick={() => this.setState({ tinder_buttons: !this.state.tinder_buttons })}
                    style={{
                        background: '#ffffff',
                        borderRadius: '50%',
                        width: 30,
                        height: 'auto',
                        padding: '10px'
                    }}
                    alt="nope"
                    src={require('../images/nope.png')}
                />
                <img
                    style={{
                        background: '#ffffff',
                        borderRadius: '50%',
                        width: 30,
                        height: 'auto',
                        padding: '10px'
                    }}
                    alt="super-like"
                    src={require('../images/super_like.png')}
                    onClick={() => this.props.action()}
                />
                <img
                    style={{
                        background: '#ffffff',
                        borderRadius: '50%',
                        width: 30,
                        height: 'auto',
                        padding: '10px'
                    }}
                    alt="like"
                    src={require('../images/like.png')}
                    onClick={() => this.props.action()}
                />
            </footer>
        );
    };

    renderSocialButtons = () => {
        return (
            <footer className="footer-container">
                <img
                    onClick={() => this.setState({ tinder_buttons: !this.state.tinder_buttons })}
                    style={{
                        background: '#ffffff',
                        borderRadius: '50%',
                        width: 30,
                        height: 'auto',
                        padding: '10px'
                    }}
                    alt="nope"
                    src={require('../images/nope.png')}
                />
                <a href="https://github.com/jeremydanner" rel="noopener noreferrer" target="_blank">
                    <img
                        style={{ width: 46, height: 46 }}
                        alt="github"
                        src={require('../images/github.png')}
                    />
                </a>

                <a
                    href="https://www.linkedin.com/in/jeremydanner/"
                    rel="noopener noreferrer"
                    target="_blank"
                >
                    <img
                        style={{ width: 46, height: 46 }}
                        alt="linkedin"
                        src={require('../images/linkedin.png')}
                    />
                </a>

                <a
                    href="mailto:jeremydanner2@gmail.com?Subject=Tnk%20p%20att%20jag%20r%20njd%20med%20mitt%20jobb%20som%20jag%20har%20idag,%20du%20mste%20locka%20med%20ngot%20riktigt%20bra%20i%20s%20fall!%20"
                    style={{
                        background: '#ffffff',
                        borderRadius: '50%',
                        width: 30,
                        height: 30,
                        padding: '10px',
                        textAlign: 'center'
                    }}
                >
                    <span role="img" aria-label="emoji">
                        
                    </span>
                </a>
                <a
                    href="tel:+46762281885"
                    style={{
                        background: '#ffffff',
                        borderRadius: '50%',
                        width: 30,
                        height: 30,
                        padding: '10px',
                        textAlign: 'center'
                    }}
                >
                    <span role="img" aria-label="emoji">
                        
                    </span>
                </a>
            </footer>
        );
    };
    render() {
        return this.state.tinder_buttons ? this.renderTinderButtons() : this.renderSocialButtons();
    }
}



